.header {
  width: 100%;
  margin: 0 auto;
}

.wrapper {
  background-image: url(../../img/index/wrapper.png);
  background-repeat: no-repeat;
  background-position: center;
  height: 940px;
}

.header-wrapper {
  max-width: 1600px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  padding-top: 24px;
  padding-left: 200px;
}

.menu__headr-list {
  display: flex;
  padding-left: 0;
}

.menu__header-item {
  list-style: none;
}

.menu__header-link {
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  color: #2C2F36;
  text-decoration: none;
  padding-right: 72px;
  position: relative;
  content: "";
}

.menu__header-link:hover {
  color: #FB8C00;
}

.menu__header-link:active,
.menu__header-link:focus {
  color: #cf7404;
}

.menu__header-link--active:hover:before {
  position: absolute;
  content: "";
  width: 81px;
  height: 3px;
  background-color: #FB8C00;
  bottom: -5px;
  left: 0;
}

.button__print {
  font-weight: bold;
  font-size: 16px;
  line-height: 140%;
  color: #FFFFFF;
  background: #FB8C00;
  border-radius: 88px;
  padding-left: 58px;
  padding-right: 30px;
  padding-top: 13px;
  padding-bottom: 13px;
  border: none;
  position: relative;
  content: "";
}

.button__print:hover {
  background-color: #2F80ED;
}

.button__print:active,
.button__print:focus {
  background-color: #0649a0;
}

.button__print::before {
  position: absolute;
  content: "";
  background-image: url(../../img/index/print.svg);
  width: 12px;
  height: 13px;
  left: 32px;
  bottom: 17px;
}

.header__logo {
  padding-right: 314px;
}

.about-wrapper {
  max-width: 1600px;
  margin: 0 auto;
  padding-left: 200px;
  padding-bottom: 300px;
}

.about {
  display: flex;
}

.about-title {
  font-weight: 800;
  font-size: 72px;
  line-height: 88px;
  color: #2C2F36;
  max-width: 370px;
  padding-top: 109px;
  margin-bottom: 199px;
  position: relative;
  content: "";
}

.about-title::after {
  position: absolute;
  content: "";
  width: 72px;
  height: 3px;
  background-color: #FB8C00;
  bottom: -32px;
  left: 0;
}

span {
  color: #FB8C00;
}

.about-list {
  display: flex;
  margin: 0;
  list-style: none;
}
.about-list__item:first-child {
  padding-left: 0;
  max-width: 162px;
}
.about-list__item:nth-child(2) {
  max-width: 195px;
}
.about-list__item:last-child {
  max-width: 120px;
}

.about-list__text {
  font-weight: 500;
  font-size: 16px;
  line-height: 140%;
  color: #2C2F36;
}

.about-list__item {
  padding-left: 63px;
}

.about-picture__img {
  padding-top: 80px;
}

.main {
  width: 100%;
}

.catalog-wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding-top: 73px;
  padding-bottom: 194px;
}

.title {
  font-weight: 800;
  font-size: 48px;
  line-height: 59px;
  color: #2C2F36;
}

.catalog__title {
  padding-bottom: 68px;
}

.products__subtitle {
  font-weight: bold;
  font-size: 18px;
  line-height: 140%;
  color: #2C2F36;
  max-width: 315px;
}

.products__text {
  font-weight: 500;
  font-size: 16px;
  line-height: 140%;
  color: #2C2F36;
  width: 228px;
  height: 44px;
  padding-top: 16px;
  max-width: 270px;
}

.products {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 126px 30px;
  justify-items: center;
  align-items: center;
}

.products__item {
  text-align: center;
}

.work-wrapper {
  max-width: 1600px;
  margin: 0 auto;
  padding-bottom: 350px;
  padding-right: 200px;
}

.work__text {
  font-weight: bold;
  font-size: 18px;
  line-height: 140%;
  color: #2C2F36;
}

.work-container {
  display: flex;
  justify-content: space-between;
  padding-top: 64px;
}

.work__title {
  padding-left: 200px;
}

.work__item {
  max-width: 128px;
}

.work__item:first-child {
  max-width: 378px;
}

.work__item:last-child {
  max-width: 136px;
}

.work__list {
  padding-left: 130px;
}

.footer {
  max-width: 1200px;
  margin: 0 auto;
  padding-top: 180px;
}

@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 400;
  src: url(../../fonts/gilroy-regular.ttf);
}
@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 700;
  src: url(../../fonts/gilroy-bold.ttf);
}
@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 500;
  src: url(../../fonts/gilroy-medium.ttf);
}
@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 600;
  src: url(../../fonts/gilroy-semibold.ttf);
}
@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 800;
  src: url(../../fonts/Gilroy-ExtraBold.otf);
}
* {
  font-family: "Gilroy", "Arial", sans-serif;
  font-style: normal;
}

* {
  margin: 0;
  padding: 0;
}

* {
  box-sizing: content-box;
}

*::before,
*::after {
  box-sizing: inherit;
}

ul li {
  list-style: none;
}

a {
  text-decoration: none;
}